<template>
    <div class="page-container" @click="bodyClick()">
        <div class="article-container">
            <ul class="article-info">
                <li class="bname">
                    {{ info.boardName }}
                </li>
                <li class="bsubject">
                   {{ info.subject }}
                    <span v-if="info.secret=='yes'">
                       <font-awesome-icon icon="fa-solid fa-lock"/>
                    </span>
                </li>
                <li class="summary">
                    <div style="padding-top:7px;">
                        <span>작성자 : {{ info.userName }}</span>
                        <span v-if="replyShow">댓글수 : {{ commentCnt}}</span>
                        <span>조회수 : {{ info.hit }}</span>
                        <span>등록일 : {{ info.createAt }}</span>
                        <span>공개여부 : {{ info.open }}</span>
                    </div>

                    <div>
                        <v-btn class="white-button" @click="modifyItem()" depressed><span>수정</span></v-btn>
                        <v-btn class="red-button" @click="deleteItem()" depressed><span>삭제</span></v-btn>
                    </div>
                </li>
            </ul>
            <div v-if="info.imgName" class="img">
                <img :src="imgUrl"/>
            </div>
            <div v-html="info.content" class="content">

            </div>
            <div class="keyword-file">
                <div v-if="keywords.length>0">
                    <div class="item-title">태그</div>
                    <ul class="keyword-ul">
                        <li v-for="keyword in keywords">#{{keyword}}</li>
                    </ul>
                </div>
                <div v-if="dfileList.length>0">
                    <div class="item-title">첨부파일</div>
                    <ul class="file-ul">
                        <li v-for="dfile in dfileList">{{dfile.orgFileName}}</li>
                    </ul>
                </div>
            </div>
            <div class="comment-info">
                <commentList v-if="replyShow" @updateCommentCnt="updateCommentCnt" ptype="board"
                             :pid="id" :boardId="bid"></commentList>
            </div>
        </div>

    </div>
</template>


<script>

    import JQ from "jquery";
    import commentList from '@/views/ADMIN/comment/commentList'
    import UTIL from '@/common/util'
    import apiService from "@/config/admin/apiService";

    export default {
        props: ["info"],
        components: {
            commentList

        },
        data() {
            return {
                replyShow: false,
                id: '',
                bid:'',
                commentCnt: 0,
                dfileList: [],
                keywords: [],
                imgUrl: '',
            };
        },
        created() {
            if (this.info.replyUse == 'yes') this.replyShow = true;
            this.id = this.info.id;
            this.bid = this.info.bid;
            if (this.info.keywords) {
                let keywords = this.info.keywords.split(',');
                for (const key of keywords) {
                    if (key) {
                        this.keywords.push(key);
                    }
                }

            }
            if (this.info.dfileOrgNames) {
                let fileOrgNames = this.info.dfileOrgNames.split(',');
                let fileNames = this.info.dfileNames.split(',');
                let fIndex=0;
                for(const fname of fileOrgNames) {
                    let file = fileNames[fIndex];
                    this.dfileList.push({fileName:fname,orgFileName:file})
                    fIndex++;
                }

            }

        },
        methods: {
            modifyItem() {
                this.$emit('updateItem', {type: 'update'})
            },
            bodyClick() {
                this.$eventBus.$emit('bodyClick')

            },
            deleteItem() {
                const params = {
                    type: 'confirm',
                    message: '게시글을 삭제 하시겠습니까?',
                    doAction: () => {
                        this.$emit('updateItem', {type: 'delete'})
                    }
                }
                this.$eventBus.$emit('modalOpen', params);

            },
            updateCommentCnt(params) {
                this.commentCnt = UTIL.numberFormat(params.total)
            }
        },
        mounted() {

            JQ('.content img').css({'max-width': '100%'});
        },
        update() {

            JQ('.content img').css({'max-width': '100%'});
        }
    }
</script>

