<template>
    <div class="board-article-list">
        <div class="table-box">
            <form novalidate @submit.prevent="search">
                <div class="search-box">
                    <div style="display: flex;justify-content: space-between">
                        <div class="sub-title" style="padding-left:10px;">
                            게시글 검색
                        </div>
                        <v-btn depressed color="#000" style=""
                               @click="$router.push('/admin/run/boardArticleRegist/new')">
                            <span style="color:#fff;">게시글 작성</span>
                        </v-btn>
                    </div>
                    <table class="table-search" cellspacing="0" cellpadding="0" border="0">
                        <tbody>
                        <tr>
                            <th style="border-radius:5px">공개여부</th>
                            <td style="border-bottom:solid 1px #999;height:50px;">
                                <div style="display: flex;max-height:40px;margin-top:-25px;">
                                    <v-checkbox v-model="open" value="yes" label="YES"></v-checkbox>
                                    <span style="padding:0 10px;">&nbsp;</span>
                                    <v-checkbox v-model="open" value="no" label="NO"></v-checkbox>
                                </div>
                            </td>
                            <th style="border-left:solid 1px #999;border-bottom:solid 1px #999;">공지여부</th>
                            <td style="border-bottom:solid 1px #999;">
                                <div style="display: flex;max-height:40px;margin-top:-25px;">
                                    <v-checkbox v-model="notice" value="yes" label="YES"></v-checkbox>
                                    <span style="padding:0 10px;">&nbsp;</span>
                                    <v-checkbox v-model="notice" value="no" label="NO"></v-checkbox>
                                </div>
                            </td>
                            <th style="border-left:solid 1px #999;border-bottom:solid 1px #999;">댓글허용여부</th>
                            <td style="border-bottom:solid 1px #999;">
                                <div style="display: flex;max-height:40px;margin-top:-25px;">
                                    <v-checkbox v-model="reply" value="yes" label="YES"></v-checkbox>
                                    <span style="padding:0 10px;">&nbsp;</span>
                                    <v-checkbox v-model="reply" value="no" label="NO"></v-checkbox>
                                </div>
                            </td>
                        </tr>
                        <tr>
                            <th>검색어</th>
                            <td colspan="5">

                                <div class="select-box" style="display: flex;max-height:40px;padding-top:6px;">
                                    <v-select v-model="keywordCmd"
                                              dense
                                              style="max-width:100px;"
                                              :items="keywordCmdList">

                                    </v-select>
                                    <div class="input-box" style="margin-left:20px;width:300px;">
                                        <v-text-field
                                                dense
                                                v-model="keyword"
                                        ></v-text-field>
                                    </div>
                                </div>
                            </td>

                        </tr>
                        <tr>
                            <th style="border-radius:5px">일자 검색</th>
                            <td colspan="5">
                                <div style="display: flex;max-height: 40px;padding-top:6px;">
                                    <v-select v-model="dateCmd"
                                              dense
                                              style="max-width:100px;margin-right:20px;"
                                              :items="dateCmdList">

                                    </v-select>
                                    <div class="date-box">
                                        <v-btn depressed :class="'white-button '+dayClass.to" @click="setDay('to')">오늘
                                        </v-btn>
                                        <v-btn depressed :class="'white-button '+dayClass.yer" @click="setDay('yer')">
                                            어제
                                        </v-btn>
                                        <v-btn depressed :class="'white-button '+dayClass.thr" @click="setDay('thr')">
                                            3일
                                        </v-btn>
                                        <v-btn depressed :class="'white-button '+dayClass.week" @click="setDay('week')">
                                            1주일
                                        </v-btn>
                                        <v-btn depressed :class="'white-button '+dayClass.mon" @click="setDay('mon')">
                                            1개월
                                        </v-btn>
                                        <v-btn depressed :class="'white-button '+dayClass.tmon" @click="setDay('tmon')">
                                            3개월
                                        </v-btn>
                                        <v-btn depressed :class="'white-button '+dayClass.all" @click="setDay('all')">
                                            전체
                                        </v-btn>
                                    </div>
                                    <div style="display: flex;margin-top:-11px;margin-left:10px;">
                                        <v-menu
                                                v-model="vmenu1"
                                                :close-on-content-click="false"
                                                transition="scale-transition"
                                                offset-y
                                                min-width="auto"
                                        >
                                            <template v-slot:activator="{ on, attrs }">
                                                <div class="input-box" style="width:120px;">
                                                    <v-text-field
                                                            v-model="stdate"
                                                            readonly
                                                            v-bind="attrs"
                                                            v-on="on"
                                                    ></v-text-field>
                                                </div>
                                            </template>
                                            <v-date-picker
                                                    v-model="stdate"
                                                    @input="vmenu1 = false"
                                            ></v-date-picker>
                                        </v-menu>

                                        <div style="width:30px;text-align: center;line-height: 50px;height:50px;">~
                                        </div>

                                        <v-menu
                                                v-model="vmenu2"
                                                :close-on-content-click="false"
                                                transition="scale-transition"
                                                offset-y
                                                min-width="auto"
                                        >
                                            <template v-slot:activator="{ on, attrs }">
                                                <div class="input-box" style="width:120px;">
                                                    <v-text-field
                                                            v-model="endate"
                                                            readonly
                                                            v-bind="attrs"
                                                            v-on="on"
                                                    ></v-text-field>
                                                </div>
                                            </template>
                                            <v-date-picker
                                                    v-model="endate"
                                                    @input="vmenu2 = false"
                                            ></v-date-picker>
                                        </v-menu>
                                    </div>
                                </div>
                            </td>
                        </tr>
                        </tbody>
                    </table>
                </div>
                <div class="button-row">
                    <v-btn class="white-button" depressed @click="searchReset()"><span>초기화</span></v-btn>
                    <v-btn class="search-button" type="submit" depressed><span>검색</span></v-btn>
                </div>
            </form>
            <div style="display: flex;padding:0 10px;justify-content: space-between;z-index: 20;">
                <div class="sub-title" style="display:flex;">게시글 리스트
                    <v-btn depressed color="primary" outlined style="margin-top:-5px;" class="sdel-button" rounded
                           @click="deleteArticle()">
                        선택삭제
                    </v-btn>
                    <div class="download-row" style="padding-left:30px;margin-top:-8px;">

                        <v-btn type="button" depressed class="black-button" @click="excelDown()">검색결과 전체게시글 엑셀다운로드
                        </v-btn>
                    </div>
                </div>
                <div class="select-box" style="display:flex;max-height:40px;padding-top:4px;">
                    <div class="total">전체게시글수 {{ util.numberFormat(paging.total) }}개</div>
                    <v-select v-model="datalimit"
                              dense
                              style="max-width:100px;"
                              :items="datalimitList">

                    </v-select>

                </div>
            </div>
            <shopTable :datas="articleList" :fixed="fixed" @pageMove="pageMove" :updateStickyTop="updateStickyTop"
                       :fixedWidth="fixedWidth"
                       @parentEvent="tableEventAction" @articleView="articleView" :pagingReload="pagingReload"
                       :columns="columns" :paging="paging"
                       @checkDataList="checkDataList"></shopTable>
        </div>
        <v-dialog
                v-model="articleDialog"
                width="1200"
                persistent
                rounded
                style="z-index:100;"
        >
            <div class="modal-layout" style="height:700px;background: #fff;">
                <div class="modal-header">
                    <div class="modal-title">게시글 보기</div>
                    <div class="modal-close" @click="articleDialog=false">
                        <font-awesome-icon icon="fa-solid fa-circle-xmark"/>
                    </div>
                </div>
                <div class="modal-body" style="height:570px;overflow-x:hidden;" id="articleView-container">
                    <boardArticleView @updateItem="updateItem" v-if="articleDialog"
                                      :info="selectedArticle"></boardArticleView>

                </div>
                <div class="modal-button-row" style="text-align: center;padding-top:15px;">
                    <v-btn class="white-button" style="width:120px;border:solid 2px #555;" rounded
                           @click="articleDialog=false" depressed>닫기
                    </v-btn>
                </div>
            </div>
        </v-dialog>

        <div style="display:none">
            <table border="1" id="excelTable">
                <thead>
                <tr>
                    <th>카테고리</th>
                    <th >제목</th>
                    <th >내용</th>
                    <th>작성자</th>
                    <th>파일첨부</th>
                    <th>공개여부</th>
                    <th>공지여부</th>
                    <th>댓글허용여부</th>
                    <th>댓글수</th>
                    <th>조회수</th>
                    <th>수정일</th>
                    <th>등록일</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(item,index) in excelList">
                    <td>{{item.boardName}}</td>
                    <td >{{item.subject}}</td>
                    <td>{{item.content}}</td>
                    <td>{{item.userName}}</td>
                    <td>{{item.file}}</td>
                    <td>{{item.open}}</td>
                    <td>{{item.notice}}</td>
                    <td>{{item.reply}}</td>
                    <td>{{item.commentCnt}}</td>
                    <td>{{item.hit}}</td>
                    <td>{{item.updateAt}}</td>
                    <td>{{item.createAt}}</td>
                </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>


<script>
    import apiService from '@/config/admin/apiService'
    import {
        API_ADMIN_GET_BOARD_ARTICLE_LIST,
        API_ADMIN_DELETE_BOARD_ARTICLE, API_ADMIN_GET_MEMBER_ALL_LIST
    } from '@/config/admin/urls'
    import UTIL from '@/common/util'
    import shopTable from '@/components/shopTable'
    import boardArticleView from './boardArticleView'

    export default {
        props: [
            "caegoryList",
            "boardIdActive"
        ],
        components: {
            shopTable,
            boardArticleView
        },
        watch: {
            boardIdActive(val) {
                if (val) {
                    this.searchReset();
                    this.setInit()
                }
            },
            datalimit(val) {
                this.getDataList(1);
            },

        },
        data() {
            return {
                util: UTIL,
                columns: [
                    {
                        name: '선택',
                        id: 'id',
                        sort: false,
                        width: 60,
                        type: 'allCheckbox',
                    },
                    {
                        name: '번호',
                        id: 'number',
                        sort: false,
                        width: 60,
                    },
                    {
                        name: '카테고리',
                        id: 'boardName',
                        width: 250,
                    },
                    {
                        name: '제목',
                        id: 'subject',
                        width: 450,
                        textStyle: {
                            'text-align': 'left',
                            'padding-left': '10px'
                        },
                        type: 'underline-action',
                        action: 'articleView',
                    },
                    {
                        name: '작성자',
                        id: 'userName',
                        width: 100,
                    },
                    {
                        name: '파일첨부',
                        id: 'file',
                        width: 100,
                    },
                    {
                        name: '공개여부',
                        id: 'open',
                        width: 100,
                        sort: true,
                    },
                    {
                        name: '공지여부',
                        id: 'notice',
                        width: 100,
                        sort: true,
                    },
                    {
                        name: '댓글허용여부',
                        id: 'reply',
                        width: 140,
                        sort: true,
                    },
                    {
                        name: '댓글수',
                        id: 'commentCnt',
                        width: 80,
                        sort: true,
                    },
                    {
                        name: '조회수',
                        id: 'hit',
                        sort: true,
                        width: 80
                    },
                    {
                        name: '수정일',
                        id: 'updateAt',
                        sort: true,
                        width: 120
                    },
                    {
                        name: '등록일',
                        id: 'createAt',
                        sort: true,
                        width: 120
                    },


                ],
                updateStickyTop: 0,
                ids: [],
                fixed: 3,
                fixedWidth: 370,
                articleList: [],
                bid: '',
                category: '',
                boardNames: {},
                //
                boardIds: [],
                categorys: [],
                categoryNames: {},
                //
                paging: {
                    total: '',
                    links: [],
                    currentPage: 1,
                    lastPage: 1,
                },
                page: 1,
                datalimit: 20,
                dataSort: {
                    field: 'createAt',
                    sort: 'desc'
                },
                keywordCmd: 'subject',
                keywordCmdList: [
                    {text: '제목', value: 'subject'},
                    {text: '작성자', value: 'userName'},

                ],
                keyword: '',
                dateCmd: 'createAt',
                dateCmdList: [
                    {text: '등록일', value: 'createAt'},
                    {text: '수정일', value: 'updateAt'},
                ],
                stdate: '',
                endate: '',
                vmenu1: false,
                vmenu2: false,
                datalimitList: [
                    {text: '10개', value: 10},
                    {text: '20개', value: 20},
                    {text: '30개', value: 30},
                    {text: '50개', value: 50},
                    {text: '100개', value: 100},
                ],
                dayClass: {
                    all: 'active',
                    to: '',
                    yer: '',
                    thr: '',
                    week: '',
                    mon: '',
                    tmon: '',
                },
                boardDatas: {},
                articleDialog: false,
                selectedArticle: {},
                selectedId: '',
                pagingReload: false,
                reply: [],
                open: [],
                notice: [],
                excelDownActive:false,
                excelList:[],
                total:0,

            }
        },
        created() {
            if (this.caegoryList.length > 0) {
                this.setInit()
            }

        },
        methods: {
            setInit() {
                this.categoryNames = {};
                this.boardIds = [];
                this.categorys = [];
                this.boardAllIds = [];
                this.reply = []
                this.open = []
                this.notice = []
                this.total = 0;

                for (const item of this.caegoryList) {

                    if (item.linkType == 'board') {
                        if (item.active) {
                            this.boardIds.push(item.boardId);
                        }
                        this.categoryNames[item.boardId] = {
                            mname: item.mname,
                            bname: '',
                            categorys: '',
                        }
                        this.boardAllIds.push(item.boardId);
                    } else if (item.subList && item.open) {
                        for (const subItem of item.subList) {
                            if (subItem.active) {

                                this.boardIds.push(subItem.boardId);
                            }
                            let categorys = {};
                            if (subItem.subCateList && subItem.subCateList.length > 0) {
                                for (const subCate of subItem.subCateList) {
                                    if (subCate.active && subItem.open) {
                                        categorys[subCate.code] = subCate.mname;
                                        this.categorys.push({
                                            bid: subItem.boardId,
                                            code: subCate.code
                                        });
                                    }
                                }
                            }
                            this.categoryNames[subItem.boardId] = {
                                mname: item.mname,
                                bname: subItem.mname,
                                categorys: categorys,
                            }
                            this.boardAllIds.push(subItem.boardId);
                        }
                    }

                }
                this.setDay('all');
                this.getDataList(1);
            },
            setDay(cmd) {
                for (let key in this.dayClass) {
                    if (key == cmd) {
                        this.dayClass[key] = 'active';
                    } else {
                        this.dayClass[key] = '';
                    }
                }
                const nowDate = new Date();
                let mon = nowDate.getMonth() + 1;
                let day = nowDate.getDate();
                const nowDay = nowDate.getFullYear() + '-' + ((mon < 10) ? '0' + mon : mon) + '-' + ((day < 10) ? '0' + day : day);
                this.endate = nowDay;
                switch (cmd) {
                    case 'all':
                        this.stdate = '';
                        this.endate = '';
                        break;
                    case 'to':
                        this.stdate = nowDay;
                        break;
                    case 'yer':
                        nowDate.setDate(day - 1);
                        mon = nowDate.getMonth() + 1;
                        day = nowDate.getDate();
                        this.stdate = nowDate.getFullYear() + '-' + ((mon < 10) ? '0' + mon : mon) + '-' + ((day < 10) ? '0' + day : day);
                        break;
                    case 'thr':
                        nowDate.setDate(day - 3);
                        mon = nowDate.getMonth() + 1;
                        day = nowDate.getDate();
                        this.stdate = nowDate.getFullYear() + '-' + ((mon < 10) ? '0' + mon : mon) + '-' + ((day < 10) ? '0' + day : day);
                        break;
                    case 'week':
                        nowDate.setDate(day - 7);
                        mon = nowDate.getMonth() + 1;
                        day = nowDate.getDate();
                        this.stdate = nowDate.getFullYear() + '-' + ((mon < 10) ? '0' + mon : mon) + '-' + ((day < 10) ? '0' + day : day);
                        break;
                    case 'mon':
                        nowDate.setMonth(mon - 1);
                        mon = nowDate.getMonth();
                        day = nowDate.getDate();
                        this.stdate = nowDate.getFullYear() + '-' + ((mon < 10) ? '0' + mon : mon) + '-' + ((day < 10) ? '0' + day : day);
                        break;
                    case 'tmon':
                        nowDate.setMonth(mon - 3);
                        mon = nowDate.getMonth();
                        day = nowDate.getDate();
                        this.stdate = nowDate.getFullYear() + '-' + ((mon < 10) ? '0' + mon : mon) + '-' + ((day < 10) ? '0' + day : day);
                        break;

                }
            },
            searchReset() {
                this.keywordCmd = 'subject'
                this.keyword = '';
                this.stdate = '';
                this.endate = '';
                this.bid = '';
                this.category = '';
                this.dateCmd = 'createAt';
                this.reply = []
                this.open = []
                this.notice = []
                this.total = 0;
                this.setDay('mon');

            },
            getSearchParams(page) {
                let boardIds = [];
                if (this.boardIds.length < 1) {
                    boardIds = this.boardAllIds;
                } else {
                    boardIds = this.boardIds;
                }
                let reply = '';
                if (this.reply.length == 1) {
                    reply = this.reply[0];
                }
                let open = '';
                if (this.open.length == 1) {
                    open = this.open[0];
                }
                let notice = '';
                if (this.notice.length == 1) {
                    notice = this.notice[0];
                }
                this.page = page;
                let frm = {
                    orderByField: this.dataSort.field,
                    orderBySort: this.dataSort.sort,
                    limit: this.datalimit.toString(),
                    page: page.toString(),
                    keywordCmd: this.keywordCmd,
                    keyword: this.keyword,
                    dateCmd: this.dateCmd,
                    stdate: this.stdate.toString(),
                    endate: this.endate.toString(),
                    boardIds: boardIds,
                    categorys: this.categorys,
                    reply:reply,
                    open:open,
                    notice:notice

                }
                return frm;
            },
            getDataList(page) {
                const frm = this.getSearchParams(page);
                this.$eventBus.$emit('overlay', 'white');
                apiService.post(API_ADMIN_GET_BOARD_ARTICLE_LIST, frm, (res) => {
                    this.setDataList(res.articleData);

                });
            },
            setDataList(datas) {
                this.articleList = [];
                this.paging = {
                    total: datas.total,
                    currentPage: this.page,
                    pageGroup: 10,
                    limit: this.datalimit
                }
                this.total = datas.total;
                const startNumber = parseInt(datas.total) - (this.datalimit * (parseInt(this.page) - 1));
                let i = 0;
                for (let item of datas.list) {
                    item.number = startNumber - i;
                    item.createAt = (item.createAt) ? item.createAt.substring(0, 16).replace('T', ' ') : '';
                    item.updateAt = (item.updateAt) ? item.updateAt.substring(0, 16).replace('T', ' ') : '';

                    item.orgBid = item.bid;

                    item.open = (item.open == 'yes') ? '공개' : '비공개';
                    item.notice = (item.notice == 'yes') ? '공지' : '일반';
                    item.file = (item.dfileNames) ? '첨부' : '미첨부';
                    item.reply = (item.replyUse=='yes')?'댓글허용':'댓글미허용';
                    if (item.category && this.categoryNames[item.bid] && this.categoryNames[item.bid].categorys && this.categoryNames[item.bid].categorys[item.category]) {
                        item.boardName = this.categoryNames[item.bid].mname + ' > ' + this.categoryNames[item.bid].bname + ' > ' + this.categoryNames[item.bid].categorys[item.category]
                    } else if (this.categoryNames[item.bid]) {
                        item.boardName = this.categoryNames[item.bid].mname + ' > ' + this.categoryNames[item.bid].bname;
                    }
                    item.commentCnt = (item.commentCnt) ? UTIL.numberFormat(item.commentCnt) : 0;
                    item.hit = (item.hit) ? UTIL.numberFormat(item.hit) : 0;
                    this.articleList.push(item)
                    i++;
                }
                this.pagingReload = true;
                setTimeout(() => {
                    this.$eventBus.$emit('overlay', 'hide');
                    this.pagingReload = false;
                }, 300)
            },
            pageMove(params) {
                this.getDataList(params.page);
            },
            tableEventAction(params) {
                switch (params.type) {
                    case 'dataSort':
                        this.dataSort = params.sort;
                        this.getDataList(1);
                        break;
                }

            },
            search() {
                this.getDataList(1);
            },
            checkDataList(params) {
                this.ids = []
                for (let key in params) {
                    this.ids.push(key);
                }
            },
            articleView(params) {
                this.selectedId = params.id;
                for (let item of this.articleList) {
                    if (item.id == params.id) {
                        this.selectedArticle = item;
                        break;
                    }
                }
                this.articleDialog = true;

            },
            deleteArticle() {
                if (this.ids.length < 1) {
                    const params = {
                        message: '게시글을 하나 이상 선택하세요',
                    }
                    this.$eventBus.$emit('modalOpen', params);
                    return;
                }
                const dparams = {
                    type: 'confirm',
                    message: '한번삭제된 게시글은 복원이 불가능합니다<br>게시글을 삭제 하시겠습니까?',
                    doAction: () => {
                        this.deleteAction(this.ids);
                    }
                }
                this.$eventBus.$emit('modalOpen', dparams);

            },
            updateItem(params) {
                if (params.type == 'update') {
                    if (this.selectedId) {
                        this.articleDialog = false;
                        this.$router.push('/admin/run/boardArticleRegist/' + this.selectedId)

                    }
                } else if (params.type == 'delete') {
                    if (this.selectedId) {
                        this.deleteAction([this.selectedId]);
                    }
                }
            },
            deleteAction(ids) {
                const frm = {
                    ids: ids,
                }
                this.$eventBus.$emit('overlay', 'open');
                apiService.post(API_ADMIN_DELETE_BOARD_ARTICLE, frm, (res) => {
                    this.$eventBus.$emit('overlay', 'hide');
                    this.articleDialog = false;
                    const params = {
                        message: '게시글이 삭제되었습니다',
                        doAction: () => {
                            this.getDataList(this.page);
                        }
                    }
                    this.$eventBus.$emit('modalOpen', params);

                })
            },
            excelDown() {
                let boardIds = [];
                if (this.boardIds.length < 1) {
                    boardIds = this.boardAllIds;
                } else {
                    boardIds = this.boardIds;
                }
                let reply = '';
                if (this.reply.length == 1) {
                    reply = this.reply[0];
                }
                let open = '';
                if (this.open.length == 1) {
                    open = this.open[0];
                }
                let notice = '';
                if (this.notice.length == 1) {
                    notice = this.notice[0];
                }
                let frm = {
                    orderByField: this.dataSort.field,
                    orderBySort: this.dataSort.sort,
                    limit: this.total.toString(),
                    page: '1',
                    keywordCmd: this.keywordCmd,
                    keyword: this.keyword,
                    dateCmd: this.dateCmd,
                    stdate: this.stdate.toString(),
                    endate: this.endate.toString(),
                    boardIds: boardIds,
                    categorys: this.categorys,
                    reply:reply,
                    open:open,
                    notice:notice

                }
                this.$eventBus.$emit('overlay', 'open');
                apiService.post(API_ADMIN_GET_BOARD_ARTICLE_LIST, frm, (res) => {
                    this.$eventBus.$emit('overlay', 'hide');
                    this.setExcelDown(res.articleData);
                });
            },
            setExcelDown(datas) {
                this.excelList = [];
                for (let item of datas.list) {
                    item.createAt = (item.createAt) ? item.createAt.substring(0, 16).replace('T', ' ') : '';
                    item.updateAt = (item.updateAt) ? item.updateAt.substring(0, 16).replace('T', ' ') : '';

                    item.orgBid = item.bid;
                    item.open = (item.open == 'yes') ? '공개' : '비공개';
                    item.notice = (item.notice == 'yes') ? '공지' : '일반';
                    item.file = (item.dfileNames) ? '첨부' : '미첨부';
                    item.reply = (item.replyUse=='yes')?'댓글허용':'댓글미허용';
                    if (item.category && this.categoryNames[item.bid] && this.categoryNames[item.bid].categorys && this.categoryNames[item.bid].categorys[item.category]) {
                        item.boardName = this.categoryNames[item.bid].mname + ' > ' + this.categoryNames[item.bid].bname + ' > ' + this.categoryNames[item.bid].categorys[item.category]
                    } else if (this.categoryNames[item.bid]) {
                        item.boardName = this.categoryNames[item.bid].mname + ' > ' + this.categoryNames[item.bid].bname;
                    }
                    item.commentCnt = (item.commentCnt) ? UTIL.numberFormat(item.commentCnt) : 0;
                    item.hit = (item.hit) ? UTIL.numberFormat(item.hit) : 0;
                    this.excelList.push(item)
                }
                this.excelDownActive = true;


            },
            s2ab(s) {
                const buf = new ArrayBuffer(s.length); //convert s to arrayBuffer
                const view = new Uint8Array(buf);  //create uint8array as viewer
                for (var i = 0; i < s.length; i++) view[i] = s.charCodeAt(i) & 0xFF; //convert to octet
                return buf;
            },
        },
        updated() {
            if (this.excelDownActive) {
                const wb = window.XLSX.utils.book_new();
                const workSheet = window.XLSX.utils.table_to_sheet(document.getElementById('excelTable'));
                const fileName = '전체 게시글 목록.xlsx';
                const sheetName = '전체 게시글 목록';
                // step 3. workbook에 새로만든 워크시트에 이름을 주고 붙인다.
                window.XLSX.utils.book_append_sheet(wb, workSheet, sheetName);
                // step 4. 엑셀 파일 만들기
                const wbout = window.XLSX.write(wb, {bookType: 'xlsx', type: 'binary'});
                // step 5. 엑셀 파일 내보내기
                window.saveAs(new Blob([this.s2ab(wbout)], {type: "application/octet-stream"}), fileName);
                this.excelDownActive = false;
            }
        }

    }
</script>

