<template>
    <div class="data-container">
        <div class="siteMenu-list">
            <div class="siteMenu-list-header" style="font-weight: bold;font-size:18px;">
                게시판 분류
            </div>
            <div class="siteMenu-list-body">

                <ul v-for="(item,index) in siteMenuList" class="main-siteMenu">
                    <li class="main-li">
                        <div :class="'main-data '+item.active"  @click="choicesiteMenu('main',index)">
                            <div class="icon-box" >
                                <div v-if="item.subList && item.subList.length>0">
                                    <font-awesome-icon class="icon" v-if="item.open"
                                                       icon="fa-solid fa-folder-open"/>
                                    <font-awesome-icon class="icon" v-else icon="fa-solid fa-folder"/>
                                </div>
                                <div v-else>
                                    <font-awesome-icon class="icon" icon="fa-solid fa-file-lines"/>
                                </div>
                            </div>
                            <div class="gname" >
                                {{ item.mname }}
                                <font-awesome-icon v-if="item.muse=='yes'" icon="fa-solid fa-eye"/>
                                <font-awesome-icon v-else icon="fa-solid fa-eye-slash"/>
                            </div>

                        </div>

                        <ul v-if="item.open" v-for="(subItem,subIndex) in item.subList" class="sub-siteMenu">
                            <li class="sub-li">
                                <div :class="'sub-data '+subItem.active"   @click="choicesiteMenu('sub',index,subIndex)">
                                    <div class="icon-box">
                                        <div v-if="subItem.subCateList && subItem.subCateList.length>0">
                                            <font-awesome-icon class="icon" v-if="subItem.open"
                                                               icon="fa-solid fa-folder-open"/>
                                            <font-awesome-icon class="icon" v-else icon="fa-solid fa-folder"/>
                                        </div>
                                        <div v-else>
                                            <font-awesome-icon class="icon" icon="fa-solid fa-file-lines"/>
                                        </div>
                                    </div>
                                    <div class="gname">
                                        {{ subItem.mname }}
                                        <font-awesome-icon v-if="subItem.muse=='yes'" icon="fa-solid fa-eye"/>
                                        <font-awesome-icon v-else icon="fa-solid fa-eye-slash"/>
                                    </div>

                                </div>
                                <ul v-if="subItem.open && subItem.subCateList" v-for="(cateItem,cateIndex) in subItem.subCateList" class="cate-siteMenu">
                                    <li class="cate-li">
                                        <div :class="'cate-data '+cateItem.active"   @click="choicesiteMenu('cate',index,subIndex,cateIndex)">
                                            <div class="icon-box">
                                                    <font-awesome-icon class="icon" icon="fa-solid fa-file-lines"/>

                                            </div>
                                            <div class="gname">
                                                {{ cateItem.mname }}
                                                <font-awesome-icon v-if="cateItem.muse=='yes'" icon="fa-solid fa-eye"/>
                                                <font-awesome-icon v-else icon="fa-solid fa-eye-slash"/>
                                            </div>

                                        </div>
                                    </li>
                                </ul>
                            </li>
                        </ul>
                    </li>
                </ul>
            </div>
        </div>
        <div class="article-box">
            <boardArticleList :caegoryList="siteMenuList" :boardIdActive="boardIdActive"></boardArticleList>
        </div>

    </div>
</template>


<script>
    import apiService from '@/config/admin/apiService'
    import {
        API_ADMIN_GET_SETTING_MENU
    } from '@/config/admin/urls'
    import BoardArticleList from "@/views/ADMIN/run/board/boardArticleList";
    export default {
        components: {
            BoardArticleList
        },
        watch: {

        },
        data() {
            return {
                siteMenuList: [],
                rootActive: 'active',
                serverFlag: false,
                boardIdActive:false,

            }
        },
        created() {
            this.getData();
        },
        methods: {
            getData() {
                if (this.serverFlag) return;
                this.serverFlag = true;
                apiService.post(API_ADMIN_GET_SETTING_MENU, {}, (res) => {
                    this.serverFlag = false;
                    let boardList = {}
                    if(res.boardList) {
                        for(const brd of res.boardList) {
                            let categorys = {};
                            if(brd.categoryUse=='yes') {
                                let categoryList = JSON.parse(brd.categoryList);
                                for(const cate of categoryList) {
                                    categorys[cate.code] = cate;
                                }
                            }
                            boardList[brd.bid] = {
                                name:brd.bname,
                                categorys:categorys,
                            };
                        }

                    }
                    this.siteMenuList = [];
                    if (res.setting && res.setting.menu) {
                        const menuList = JSON.parse(res.setting.menu);

                        for (let item of menuList) {
                            if(item.linkType=='board') {
                                item.active = '';
                                item.open = true;
                                this.siteMenuList.push(item);
                            } else if(item.linkType=='no') {
                                if(item.subList) {
                                    let subList = [];
                                    for(let subItem of item.subList) {
                                        if(subItem.linkType=='board') {
                                            subItem.active = '';
                                            if(boardList[subItem.boardId] && boardList[subItem.boardId].categorys) {
                                                subItem.open = true;
                                                subItem.subCateList = [];
                                                let cateCodes = {};
                                                for(const cateKey in boardList[subItem.boardId].categorys) {
                                                    let cateItem = boardList[subItem.boardId].categorys[cateKey]
                                                    cateCodes[cateItem.code] = cateItem.name;
                                                    subItem.subCateList.push({
                                                        mname:cateItem.name,
                                                        muse:cateItem.use,
                                                        code:cateItem.code,
                                                        active:'',
                                                    })

                                                }

                                            } else {
                                                subItem.open = false;

                                            }
                                            subList.push(subItem);
                                        }
                                    }
                                    if(subList.length>0) {
                                        item.open = true;
                                        item.active = '';
                                        item.subList = subList;
                                        this.siteMenuList.push(item);

                                    }
                                }
                            }

                        }
                        this.boardIdActive = true;
                        setTimeout(()=>{
                            this.boardIdActive = false;
                        },200)
                    }



                })
            },
            choicesiteMenu(cmd, index, subIndex,cateIndex) {
                let activeInfo = {};
                if (cmd == 'main') {
                    activeInfo = this.siteMenuList[index];

                    if(activeInfo.open) {
                        activeInfo.open = false;
                        activeInfo.active = '';
                        this.setSubActive(activeInfo.subList,'none')
                    } else {
                        if(activeInfo.linkType == 'board') {
                            activeInfo.active = 'active';
                        }
                        activeInfo.open = true;
                        //this.setSubActive(activeInfo.subList,'active')
                    }


                } else if(cmd=='sub') {
                    activeInfo = this.siteMenuList[index].subList[subIndex];
                    if(activeInfo.active) {
                        activeInfo.active = '';
                        if(activeInfo.subCateList) {
                            activeInfo.open = false;
                        }
                        if(activeInfo.subCateList) {
                            for(let cateItem of activeInfo.subCateList) {
                                cateItem.active=''
                            }

                        }

                        /*
                        let flag = false;
                        for(let item of this.siteMenuList[index].subList) {
                            if(item.active=='active') {
                                flag = true;
                                break;
                            }
                        }
                        if(!flag) {
                            this.siteMenuList[index].open = false;
                            this.siteMenuList[index].active = '';
                        }
                        */
                    } else {
                        activeInfo.active = 'active';
                        if(activeInfo.subCateList) {
                            activeInfo.open = true;
                            /*
                            for(let cateItem of activeInfo.subCateList) {
                                cateItem.active='active'
                            }

                             */
                        }

                    }

                } else {
                    activeInfo = this.siteMenuList[index].subList[subIndex].subCateList[cateIndex];
                    if(activeInfo.active) {
                        activeInfo.active = '';

                        /*
                        let flag = false;
                        for(let item of this.siteMenuList[index].subList[subIndex].subCateList) {
                            if(item.active=='active') {
                                flag = true;
                                break;
                            }
                        }
                        if(!flag) {
                            this.siteMenuList[index].subList[subIndex].open = false;
                            this.siteMenuList[index].subList[subIndex].active = '';
                        }

                         */
                    } else {
                        activeInfo.active = 'active';

                    }

                }
                this.boardIdActive = true;
                setTimeout(()=>{
                    this.boardIdActive = false;
                },200)


            },
            setSubActive(subList,cmd) {
                for(let item of subList) {
                    if(cmd=='active') {
                        item.active = 'active';
                        if(item.subCateList) {
                            item.open = true;
                            for(let cateItem of item.subCateList) {
                                cateItem.active='active'
                            }
                        }

                    } else {
                        item.active = '';
                        if(item.subCateList) {
                            item.open = false;
                        }
                    }
                }
            }
        }

    }
</script>

